import React from 'react';
import Global from "../Global";
import ReturnThingElement from "./ReturnThingElement";

let keyNumber = 10;

export default function AllReturnThings() {
    const myArr = Global()['returnThingsArray'];
    const mass = myArr.map((thing) => {
        keyNumber++;
        return <ReturnThingElement paramsObj={thing} key={"allReturningThingsKey" + keyNumber}/>
    });

    if(myArr.length > 0) {
        return (
            <div id="myOwnThingsToReturn">
                <br/>
                <h2>My things</h2>
                <br/>
                {mass}
            </div>
        );
    } else {
        return (
            <div id="myOwnThingsToReturn">
                <br/>
                <h2>My things</h2>
                <br/>
                <div id="emptyReturningListMessageBox" className="alert alert-success" role="alert">
                    <b>List is empty</b>
                </div>
            </div>
        );
    }
}
