import React, {Component} from 'react';
import {render} from 'react-dom';
import SignInPageContent from "./components/SignInPageContent";
import Global from "./Global";
import MainPageContent from "./components/MainPageContent";

class MainContent extends Component {
    constructor(props) {
        super(props);

        this.state = {
            number: 1
        };

        Global.mainContent = this;
    }

    changeState(stateNumber) {
        this.setState({
            number: parseInt(stateNumber)
        });
    }

    render() {
        if(this.state.number === 1) {
            return (
                <div>
                    <h1>IBM things</h1>
                    <hr/>
                    <h3 onClick={changeColorFunction} id="changeColorBtn" className="text-right" style={{cursor:'pointer'}}>&#9681;</h3>
                    <SignInPageContent/>
                </div>
            );
        } else {
            return (
                <div>
                    <h1>IBM things</h1>
                    <hr/>
                    <h3 onClick={changeColorFunction} id="changeColorBtn" className="text-right" style={{cursor:'pointer'}}>&#9681;</h3>
                    <MainPageContent/>
                </div>
            );
        }
    }
}

render(<MainContent/>, document.getElementById('root'));


const COLOR_FIST = "#4a064b";
const COLOR_SECOND = "#6f96a7";

if(!localStorage.getItem("color")) {
    localStorage.setItem("color", COLOR_FIST);
}

document.body.style.background = localStorage.getItem("color");

function changeColorFunction() {
    if(localStorage.getItem("color") === COLOR_FIST) {
        localStorage.setItem("color", COLOR_SECOND);
    } else {
        localStorage.setItem("color", COLOR_FIST);
    }

    document.body.style.background = localStorage.getItem("color");
}
