import React from 'react';
import Global from "../Global";
import sendPost from "../SendPost";

export default function ThingElement(props) {
    const {paramsObj} = props;

    const sourceName = paramsObj['source_name'];
    const sourceDescription = paramsObj['source_description'];
    const sourceNumber = paramsObj['source_number'];
    const sourceImage = paramsObj['source_image'];

    function takeSourceFunc() {
        const numberString = prompt("Input number:", "0");

        const sourceId = parseInt(paramsObj['source_id']);
        const sourceNumber = parseInt(numberString);
        const userId = parseInt(Global()['userID']);

        if(sourceNumber < 0) {
            alert("Negative number is not allowed.");
            return;
        }

        const obj = {
            user_id: userId,
            source_id: sourceId,
            number_value: sourceNumber,
        };

        sendPost('api/database/source/take', JSON.stringify(obj), (answer) => {
            const result = JSON.parse(answer)['result'];

            if(result === "TAKE_OK") {
                alert("Taking source success.");
            }

            if(result === "NOT_ENOUGH_SOURCE") {
                alert("Not enough source.")
            }

            document.getElementById("allThingsBtn").click();
        });
    }

    return (
        <div style = {{background: "#FFFFFF", padding: 10, marginTop: 7, marginBottom: 7}}>
            <table>
                <tbody>
                    <tr>
                        <td style={{paddingRight: 20}}>
                            ​<picture>
                                <img alt="Thing" src={sourceImage} width={100} height={100} className="rounded float-left"/>
                            ​</picture>
                        </td>
                        <td style={{paddingRight: 20}}>
                            <button type="button" onClick={takeSourceFunc} id={"buttonTake_" +  paramsObj['source_id']} className="btn btn-primary btn-lg">Take</button>
                        </td>
                        <td>
                            <b>Name:</b>&nbsp;{sourceName}<br/>
                            <b>Description:</b>&nbsp;{sourceDescription}<br/>
                            <b>Number:</b>&nbsp;<span className="thingCounterClass">{sourceNumber}</span><br/>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    );
}
