import React from 'react';
import FieldContent from "./FieldContent";
import sendQuery from "../sendQuery";
import Global from "../Global";

export default function SignInPageContent(props) {
    return (
        <div>
            <h2>Sign In</h2>
            <br/>
            <FieldContent paramsObj={{id : "loginField", label: "Login"}} />
            <FieldContent paramsObj={{id : "passwordField", label: "Password"}} />
            <button onClick={signInBtnClickFunction} type="button" className="btn btn-primary btn-lg" id="signInBtn">Sign In</button>
            &nbsp;
            <button onClick={clearSignInFormFunction} id="clearSignInFormBtn" type="button" className="btn btn-secondary btn-lg">Clear</button>
            <br/>
            <br/>
        </div>
    );
}

const EMPTY_LOGIN = "Login is empty.";
const EMPTY_PASSWORD = "Password is empty.";
const BAD_LOGIN = "Login contains bad chars.";
const BAD_PASSWORD = "Password contains bad chars.";
const LONG_LOGIN = "Login is very long.";
const LONG_PASSWORD = "Password is very long.";
const MAX_LENGTH = 15;
const BAD_USER = "Bad login or password.";

function alertMessage(message) {
    alert(message.toString());
    return false;
}

function normalChar(charParam) {
    const s = "abcdefghijklmnopqrstuvwxyz".toUpperCase() + "1234567890";
    const c = charParam.toUpperCase();
    return s.indexOf(c) !== -1;
}

function normalString(stringParam) {
    const s = stringParam.toUpperCase();
    for(let i = 0; i < s.length; i++) {
        const c = s.charAt(i);
        if(normalChar(c) === false) {
            return false;
        }
    }
    return true;
}

function clearSignInFormFunction() {
    const loginField = document.getElementById("loginField");
    const passwordField = document.getElementById("passwordField");
    loginField.value = "";
    passwordField.value = "";
}

function signInBtnClickFunction() {
    const loginField = document.getElementById("loginField");
    const passwordField = document.getElementById("passwordField");

    const login = loginField.value.toString();
    const password = passwordField.value.toString();

    if(login.length === 0) {
        return alertMessage(EMPTY_LOGIN);
    }

    if(password.length === 0) {
        return alertMessage(EMPTY_PASSWORD);
    }

    if(normalString(login) === false) {
        return alertMessage(BAD_LOGIN);
    }

    if(normalString(password) === false) {
        return alertMessage(BAD_PASSWORD);
    }

    if(login.length > MAX_LENGTH) {
        return alertMessage(LONG_LOGIN);
    }

    if(password.length > MAX_LENGTH) {
        return alertMessage(LONG_PASSWORD);
    }

    sendQuery("api/database/user/correct", {
        login: login,
        password: password,
    }, (answer) => {
        const obj = JSON.parse(answer);
        if(obj.result === "USER_NOT_CORRECT") {
            alert(BAD_USER);
        } else {
            Global()['departmentDescription'] = obj.departmentDescription.toString();
            Global()['login'] = obj.login.toString();
            Global()['userID'] = obj.id.toString();
            Global.mainContent.changeState(2);
        }
    });
}
