import React from "react";
import sendPost from "../SendPost";
import Global from "../Global";

export default function ReturnThingElement(props) {
    const {paramsObj} = props;

    function returnElementFunction() {
        const sourceId = parseInt(paramsObj["sourceId"]);
        const userId = parseInt(Global()['userID']);

        const numberString = prompt("Input number:", "0");
        const sourceNumber = parseInt(numberString);

        if(sourceNumber < 0) {
            alert("Negative number is not allowed.");
            return;
        }

        sendPost("api/database/source/return", JSON.stringify({
            user_id: userId,
            source_id: sourceId,
            number_value: sourceNumber,
        }), (answer) => {
            const result = JSON.parse(answer)["result"];

            if(result === "RETURN_SOURCE_OK") {
                alert("Returning source success.");
            }

            if(result === "USER_NOT_HAVE_ENOUGH_SOURCE") {
                alert("You have not enough source");
            }

            document.getElementById("myThingsBtn").click();
        });
    }

    return (
        <div style = {{background: "#FFFFFF", padding: 10, marginTop: 7, marginBottom: 7}}>
            <table>
                <tbody>
                    <tr>
                        <td style={{paddingRight: 20}}>
                            ​<picture>
                                <img alt="Thing" src={paramsObj["sourceImage"]} width={100} height={100} className="rounded float-left"/>
                            ​</picture>
                        </td>
                        <td style={{paddingRight: 20}}>
                            <button onClick={returnElementFunction} type="button" id={"returnBtn_" + paramsObj["sourceId"]} className="btn btn-primary btn-lg">Return</button>
                        </td>
                        <td>
                            <b>Name:</b>&nbsp;{paramsObj["sourceName"]}<br/>
                            <b>Description:</b>&nbsp;{paramsObj["sourceDescription"]}<br/>
                            <b>Have:</b>&nbsp;<span className="returnThingHaveNumberClass">{paramsObj["sourceHave"]}</span><br/>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    );
}
