import React, {Component} from 'react';
import Global from "../Global";
import sendGet from "../sendGet";
import AllThings from "./AllThings";
import AllReturnThings from "./AllReturnThings";

export default class MainPageContent extends Component {
    constructor(props) {
        super(props);

        Global().mainPageContent = this;

        this.state = {
            number: 0
        }
    }

    changeState(stateNumber) {
        this.setState({
            number: parseInt(stateNumber)
        });
    }

    render() {
        const departmentDescription = Global()['departmentDescription'].toString();
        const userLogin = Global()['login'].toString();

        if(this.state.number === 0) {
            return (
                <div>
                    <h2>User account</h2>
                    <br/>
                    <p>
                        <b>
                            Login:
                            <br/>
                        </b>
                        <span id="userLogin" style={{paddingLeft: 10}}>
                    {userLogin}
                </span>
                    </p>
                    <p>
                        <b>
                            Department:
                            <br/>
                        </b>
                        <span id="departmentDescription" style={{paddingLeft: 10}}>
                    {departmentDescription}
                </span>
                    </p>
                    <br/>
                    <button onClick={getAllThingsFunction} id="allThingsBtn" type="button" className="btn btn-secondary btn-lg">All things
                    </button>
                    &nbsp;
                    <button onClick={getMyThingsFunction} id="myThingsBtn" type="button" className="btn btn-secondary btn-lg">My things</button>
                    <br/>
                    <br/>
                </div>
            );
        }

        if(this.state.number === 1) {
            return (
                <div>
                    <h2>User account</h2>
                    <br/>
                    <p>
                        <b>
                            Login:
                            <br/>
                        </b>
                        <span id="userLogin" style={{paddingLeft: 10}}>
                    {userLogin}
                </span>
                    </p>
                    <p>
                        <b>
                            Department:
                            <br/>
                        </b>
                        <span id="departmentDescription" style={{paddingLeft: 10}}>
                    {departmentDescription}
                </span>
                    </p>
                    <br/>
                    <button onClick={getAllThingsFunction} id="allThingsBtn" type="button" className="btn btn-secondary btn-lg">All things
                    </button>
                    &nbsp;
                    <button onClick={getMyThingsFunction} id="myThingsBtn" type="button" className="btn btn-secondary btn-lg">My things</button>
                    <br/>
                    <br/>
                    <AllThings/>
                    <br/>
                </div>
            );
        }

        if(this.state.number === 2) {
            return (
                <div>
                    <h2>User account</h2>
                    <br/>
                    <p>
                        <b>
                            Login:
                            <br/>
                        </b>
                        <span id="userLogin" style={{paddingLeft: 10}}>
                    {userLogin}
                </span>
                    </p>
                    <p>
                        <b>
                            Department:
                            <br/>
                        </b>
                        <span id="departmentDescription" style={{paddingLeft: 10}}>
                    {departmentDescription}
                </span>
                    </p>
                    <br/>
                    <button onClick={getAllThingsFunction} id="allThingsBtn" type="button" className="btn btn-secondary btn-lg">All things
                    </button>
                    &nbsp;
                    <button onClick={getMyThingsFunction} id="myThingsBtn" type="button" className="btn btn-secondary btn-lg">My things</button>
                    <br/>
                    <br/>
                    <AllReturnThings/>
                    <br/>
                </div>
            );
        }
    }
}

function getAllThingsFunction() {
    sendGet("api/database/source/get", (answer) => {
        Global()['thingsArr'] = JSON.parse(answer.toString());
        Global().mainPageContent.changeState(1);
    });
}

function getMyThingsFunction() {
    const userId = parseInt(Global()['userID']);
    const mass = [];
    sendGet(`api/database/source/user/${userId}/get`, (answer) => {
        sendGet(`api/database/source/get`, (result) => {
            const usersSource = JSON.parse(answer);
            const allSource = JSON.parse(result);
            for(let i = 0; i < usersSource.length; i++) {
                allSource.forEach((source) => {
                    if(parseInt(usersSource[i]["have_source"]) === parseInt(source["source_id"])) {
                        mass.push({
                            sourceId: parseInt(source["source_id"]),
                            sourceName: source["source_name"],
                            sourceDescription: source["source_description"],
                            sourceHave: parseInt(usersSource[i]["have_number"]),
                            sourceImage: source["source_image"],
                        });
                    }
                });
            }
            Global().returnThingsArray = mass;
            Global().mainPageContent.changeState(2);
        });
    });
}