const URL = "http://176.119.156.90/";

export default function sendQuery(urlParam, bodyParam, callback) {
    const url = URL + urlParam;

    function controlResult(xhr) {
        xhr.onreadystatechange = function() {
            if(xhr.readyState === 4 && xhr.status === 200) {
                const answer = xhr.responseText.toString();
                callback(answer);
            }
        }
    }

    if(bodyParam === null) {
        let xhr = new XMLHttpRequest();
        xhr.open("GET", url, true);
        xhr.setRequestHeader("Content-Type","application/json;charset=UTF-8");
        xhr.send(null);
        controlResult(xhr);
    }

    if(bodyParam !== null) {
        let xhr = new XMLHttpRequest();
        xhr.open("POST", url, true);
        xhr.setRequestHeader("Content-Type","application/json;charset=UTF-8");
        const body = JSON.stringify(bodyParam);
        xhr.send(body.toString());
        controlResult(xhr);
    }
}
