import React from 'react';
import Global from "../Global";
import ThingElement from "./ThingElement";

let keyNumber = 10;

export default function AllThings() {
    const myArr = Global()['thingsArr'];
    const mass = myArr.map((thing) => {
        keyNumber++;
        return <ThingElement paramsObj={thing} key={"allThingsKey" + keyNumber} />
    });

    if(myArr.length > 0) {
        return (
            <div id="allThingsBox">
                <br/>
                <h2>All things</h2>
                <br/>
                {mass}
            </div>
        );
    } else {
        return (
            <div id="allThingsBox">
                <br/>
                <h2>All things</h2>
                <br/>
                <div id="emptyListBoxWithMessage" className="alert alert-success" role="alert">
                    <b>List is empty</b>
                </div>
            </div>
        );
    }
}
