import React from 'react';

export default function FieldContent(props) {
    const {paramsObj} = props;

    const label = paramsObj.label.toString();
    const id = paramsObj.id.toString();

    return (
        <div className="input-group mb-3">
            <div className="input-group-prepend">
                <span className="input-group-text">{label}</span>
            </div>
            <input id={id} type="text" spellCheck='false' autoComplete='off' className="form-control" placeholder={label} aria-label={label}/>
        </div>
    );
}
